$purpleLight: rgb(172, 9, 172);
$purpleDark: rgb(49, 3, 49);
$grayDark: grey;

* {
    box-sizing: border-box;
}

body{
    background-color: #8EC5FC;
    background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);

}

#side-menu{
    background: $purpleLight;
    width: 280px;
    position: absolute;
    top:0;
    bottom:0;
    height: 100vh;

    nav{
        padding-top: 100px;
        a{
            display: block;
            color: white;
            text-decoration: none;
            padding: 15px 30px;

            .fa {
                color: white;
                font-size: size 1.4rem;
                margin-right: 0.8rem;
                width:30px;
                position:relative;
            }

            &:hover{
                transition: all .3s ease-in-out;
                background:$purpleDark;
                border-left: 5px solid greenyellow;
                padding: 15px 30px 15px 25px;

                .fa {
                    color: greenyellow;
                }
            }
            &.active{
                background:$purpleDark;
                border-left: 5px solid greenyellow;
                padding: 15px 30px 15px 25px;

                .fa {
                    color: greenyellow;
                }
            }

        }
    }
}

#header{
    margin-left: 280px;
    padding: 20px;
    background-color: white;

    .search-area{
        display:inline-block;
        .fa{
            color: $grayDark;
            margin-right: 10px;
        }
        input[type="text"]{
            background: transparent;
            border: none;
            font-size: 1.3rem;
            color: greenyellow;
            padding: 10px;
            font-weight: 100;
        }
    }

    .user-area{
        float: right;
        // display:inline-block;
        padding: 10px;

        a{
            display: inline-block;
            padding-right: 20px;
            text-decoration: none;

            &.notification{
                position: relative;

                .circle{
                    display:inline-block;
                    background: red;
                    width: 16px;
                    height:16px;
                    border-radius: 50%;
                    font-size: 11px;
                    color: white;
                    // padding: 10px;
                    text-align: center;
                    position: absolute;
                    top: -7px;
                    left: 5px;
                }
            }

            .user-img{
                background: url("./selfie.JPG") no-repeat center center;
                -webkit-bakground-size: cover;
                -moz-background-size: cover;
                -o-background-size: cover;
            }
            
        }
    }
}